"use client";

import { useState} from 'react';
import { Progress } from "@/components/ui/progress";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";

export const ProjectFundingForm = () => {
  const STEPS = [
    { id: 'type', label: 'Funding Type' },
    { id: 'details', label: 'Funding Details' },
    { id: 'contact', label: 'Contact Info' },
    { id: 'review', label: 'Review & Submit' }
  ];

  const [currentStep] = useState(0);
  const [progress] = useState(25);

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-8">
      {/* Progress Indicator */}
      <Card className="p-4">
        <div className="space-y-3">
          <div className="flex justify-between items-center">
            <h3 className="text-sm font-medium">Application Progress</h3>
            <span className="text-sm text-muted-foreground">{progress}% Complete</span>
          </div>
          <Progress value={progress} className="h-2" />
          <div className="flex justify-between">
            {STEPS.map((step, index) => (
              <div
                key={step.id}
                className={`flex items-center gap-2 text-xs px-2 py-1 rounded transition-colors ${
                  index === currentStep 
                    ? 'bg-primary text-primary-foreground' 
                    : index < currentStep 
                    ? 'bg-green-100 text-green-700' 
                    : 'text-muted-foreground'
                }`}
              >
                <div className={`w-4 h-4 rounded-full flex items-center justify-center text-xs ${
                  index === currentStep 
                    ? 'bg-primary-foreground text-primary' 
                    : index < currentStep 
                    ? 'bg-green-600 text-white' 
                    : 'bg-muted-foreground/20'
                }`}>
                  {index < currentStep ? '✓' : index + 1}
                </div>
                {step.label}
              </div>
            ))}
          </div>
        </div>
      </Card>

      {/* Step Content */}
      <div className="space-y-6">
        {/* Step 1: Funding Type Selection */}
        {currentStep === 0 && (
          <div className="space-y-6">
            <div className="text-center space-y-4 p-8 bg-gradient-to-br from-primary/5 to-blue-500/5 rounded-xl">
              <h1 className="text-3xl font-bold">Fund This Project</h1>
              <p className="text-muted-foreground">Support innovation that creates meaningful impact</p>
              <div className="p-4 bg-white rounded-lg border">
                <h2 className="font-semibold">Project Title</h2>
                <p className="text-sm text-muted-foreground">Project description would appear here</p>
                <div className="flex gap-2 mt-2">
                  <Badge variant="outline">Category</Badge>
                  <Badge variant="default">Status</Badge>
                </div>
              </div>
            </div>
            
            <Card className="p-6">
              <CardHeader>
                <CardTitle>Choose Your Support Type</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid md:grid-cols-2 gap-4">
                  <div className="p-4 border rounded-lg cursor-pointer hover:border-primary/50">
                    <h4 className="font-medium">Monetary Funding</h4>
                    <p className="text-sm text-muted-foreground">Provide financial support</p>
                  </div>
                  <div className="p-4 border rounded-lg cursor-pointer hover:border-primary/50">
                    <h4 className="font-medium">Strategic Partnership</h4>
                    <p className="text-sm text-muted-foreground">Offer expertise and resources</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        )}

        {/* Step 2: Funding Details */}
        {currentStep === 1 && (
          <Card>
            <CardHeader>
              <CardTitle>Funding Details</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label>Amount</Label>
                  <Input type="number" placeholder="Enter amount" />
                </div>
                <div>
                  <Label>Currency</Label>
                  <Select>
                    <SelectTrigger>
                      <SelectValue placeholder="Select currency" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="rwf">RWF</SelectItem>
                      <SelectItem value="usd">USD</SelectItem>
                      <SelectItem value="eur">EUR</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div>
                <Label>Funding Type</Label>
                <Select>
                  <SelectTrigger>
                    <SelectValue placeholder="Select type" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="grant">Grant</SelectItem>
                    <SelectItem value="equity">Equity Investment</SelectItem>
                    <SelectItem value="loan">Loan</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div>
                <Label>Expected Outcomes</Label>
                <Textarea placeholder="Describe the expected outcomes" />
              </div>
            </CardContent>
          </Card>
        )}

        {/* Step 3: Contact Information */}
        {currentStep === 2 && (
          <Card>
            <CardHeader>
              <CardTitle>Contact Information</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label>First Name</Label>
                  <Input placeholder="Enter first name" />
                </div>
                <div>
                  <Label>Last Name</Label>
                  <Input placeholder="Enter last name" />
                </div>
              </div>

              <div>
                <Label>Email</Label>
                <Input type="email" placeholder="Enter email" />
              </div>

              <div>
                <Label>Organization Name</Label>
                <Input placeholder="Enter organization name" />
              </div>

              <div className="flex items-center space-x-2">
                <Checkbox id="privacy" />
                <Label htmlFor="privacy">I agree to the privacy policy</Label>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Step 4: Review & Submit */}
        {currentStep === 3 && (
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Review Your Application</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <h4 className="font-medium">Funding Type</h4>
                  <p className="text-muted-foreground">Monetary</p>
                </div>
                <Separator />
                <div>
                  <h4 className="font-medium">Amount</h4>
                  <p className="text-muted-foreground">5,000,000 RWF</p>
                </div>
                <Separator />
                <div>
                  <h4 className="font-medium">Contact</h4>
                  <p className="text-muted-foreground">
                    John Doe - Acme Corporation
                  </p>
                </div>
              </CardContent>
            </Card>
            
            <Card className="bg-blue-50">
              <CardHeader>
                <CardTitle>Ready to Submit?</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground">
                  Your application will be reviewed within 2-3 business days.
                </p>
              </CardContent>
            </Card>
          </div>
        )}
      </div>

      {/* Navigation */}
      <div className="flex justify-between pt-6">
        <Button variant="outline">
          {currentStep === 0 ? 'Back to Project' : 'Previous'}
        </Button>
        
        <div className="flex gap-3">
          <Button variant="outline">
            Save Draft
          </Button>
          
          <Button>
            {currentStep === STEPS.length - 1 ? 'Submit Application' : 'Next Step'}
          </Button>
        </div>
      </div>
    </div>
  );
};